<template>
  <div class="centro" :style="style">
    <a
      href="https://github.com/cesargaldor/Colorgenius"
      class="github-corner"
      aria-label="View source on GitHub"
    >
      <svg
        width="80"
        height="80"
        viewBox="0 0 250 250"
        style="fill:transparent; color:#fff; position: absolute; top: 0; border: 0; left: 0; transform: scale(-1, 1);"
        aria-hidden="true"
      >
        <path d="M0,0 L115,115 L130,115 L142,142 L250,250 L250,0 Z" />
        <path
          d="M128.3,109.0 C113.8,99.7 119.0,89.6 119.0,89.6 C122.0,82.7 120.5,78.6 120.5,78.6 C119.2,72.0 123.4,76.3 123.4,76.3 C127.3,80.9 125.5,87.3 125.5,87.3 C122.9,97.6 130.6,101.9 134.4,103.2"
          fill="currentColor"
          style="transform-origin: 130px 106px;"
          class="octo-arm"
        />
        <path
          d="M115.0,115.0 C114.9,115.1 118.7,116.5 119.8,115.4 L133.7,101.6 C136.9,99.2 139.9,98.4 142.2,98.6 C133.8,88.0 127.5,74.4 143.8,58.0 C148.5,53.4 154.0,51.2 159.7,51.0 C160.3,49.4 163.2,43.6 171.4,40.1 C171.4,40.1 176.1,42.5 178.8,56.2 C183.1,58.6 187.2,61.8 190.9,65.4 C194.5,69.0 197.7,73.2 200.1,77.6 C213.8,80.2 216.3,84.9 216.3,84.9 C212.7,93.1 206.9,96.0 205.4,96.6 C205.1,102.4 203.0,107.8 198.3,112.5 C181.9,128.9 168.3,122.5 157.7,114.1 C157.9,116.9 156.7,120.9 152.7,124.9 L141.0,136.5 C139.8,137.7 141.6,141.9 141.8,141.8 Z"
          fill="currentColor"
          class="octo-body"
        />
      </svg>
    </a>
    <h1 id="texto" class="color" v-on:click="toast(); copy()">#{{resultado}}</h1>
    <boton v-on:click="reloadPage" />
    <footer>
      <div class="box">
        <h5>Made with ðŸ’™ from Seville</h5>
      </div>
    </footer>
  </div>
</template>

<script>
import boton from "@/components/boton.vue";

export default {
  name: "colors",

  // Aqui declaramos los componentes externosque se usan en este
  components: {
    boton
  },

  // declaramos datos, que en este caso solo es un string con el color randomizado
  data: function() {
    return {
      resultado: ""
    };
  },

  /* En este caso tenemos 3 mÃ©todos: el que randomiza el color, el de copiar al portapapeles
  y el que muestra el toast */
  methods: {
    getColor() {
      var result = "";
      var characters = "ABCDEF0123456789";
      var charactersLength = characters.length;
      for (var i = 0; i < 6; i++) {
        result += characters.charAt(
          Math.floor(Math.random() * charactersLength)
        );
      }
      this.resultado = result;
    },
    copy() {
      var copyText = document.getElementById("texto");
      var textArea = document.createElement("textarea");
      textArea.value = copyText.textContent;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand("Copy");
      textArea.remove();
    },
    toast() {
      this.$toasted.show("Copied to the clipboard! ðŸ”¥", {
        type: "info",
        theme: "bubble",
        position: "top-right",
        duration: 2000
      });
    }
  },
  // aqui inicializamos la funcion previamente creada
  created: function() {
    this.getColor();
  },
  // y esto nos permite hacer una "funcion" a partir de los datos de arriba
  computed: {
    style() {
      return {
        "--colorFondo": "#" + this.resultado
      };
    }
  }
};
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.centro {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: var(--colorFondo);
  color: white;
}

.color {
  margin: 1em 0;
  font-size: 2.2em;
  cursor: pointer;
}

footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0.8em;
}

footer .box h5 {
  float: right;
  margin-right: 1em;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }

  20%,
  60% {
    transform: rotate(-25deg);
  }

  40%,
  80% {
    transform: rotate(10deg);
  }
}
</style>